"use client";

import React, { useMemo, useState } from "react";
import ProgressHeader from "./components/ProgressHeader";
import Navigation from "./components/Navigation";
import WelcomeStep from "./components/steps/WelcomeStep";
import StoryStep from "./components/steps/StoryStep";

type StepKey = "welcome" | "story" | "review";

// async function start() {
//   setLoading(true);
//   const res = await fetch("/api/sessions", {
//     method: "POST",
//     body: JSON.stringify({ assessmentSlug: assessment, userId: "demo" }),
//   });
//   const data = await res.json();
//   window.location.href = `/sessions/${data.sessionId}`;
// }

const stepsConfig: { key: StepKey; title: string }[] = [
  { key: "welcome", title: "Welcome" },
  { key: "story", title: "Your Story" },
  { key: "review", title: "Review" },
];

export default function Page() {
  const [step, setStep] = useState(0);
  const total = stepsConfig.length;

  const [storyText, setStoryText] = useState("");
  const [storyAudio, setStoryAudio] = useState<string | null>(null);

  const canNext = useMemo(() => {
    const k = stepsConfig[step].key;
    if (k === "story")
      return storyText.trim().length > 0 || Boolean(storyAudio);
    return true;
  }, [step, storyText, storyAudio]);

  const onFinish = () => {
    alert("Finished (mockup) — no data saved.");
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <ProgressHeader step={step} total={total} />

      <main className="max-w-2xl mx-auto p-4">
        {stepsConfig[step].key === "welcome" && (
          <WelcomeStep onStart={() => setStep(1)} />
        )}

        {stepsConfig[step].key === "story" && (
          <StoryStep
            storyText={storyText}
            setStoryText={setStoryText}
            setStoryAudio={setStoryAudio}
          />
        )}

        {stepsConfig[step].key === "review" && (
          <div className="bg-white p-6 rounded-xl shadow space-y-3">
            <h2 className="font-serif text-xl">Review</h2>
            <div>
              <div className="text-slate-600 text-sm">Your Story</div>
              <div className="text-slate-900 whitespace-pre-wrap">
                {storyText || "—"}
              </div>
              {storyAudio && (
                <div className="mt-2">
                  <audio controls src={storyAudio} className="w-full" />
                </div>
              )}
            </div>
          </div>
        )}

        <Navigation
          step={step}
          total={total}
          onBack={() => setStep((s) => Math.max(0, s - 1))}
          onNext={() => setStep((s) => Math.min(total - 1, s + 1))}
          onFinish={onFinish}
          nextDisabled={!canNext}
        />
      </main>
    </div>
  );
}
