"use client";

import React, { useEffect, useRef, useState } from "react";
import { Mic, Square, Play, Pause, Trash2 } from "lucide-react";
import { theme } from "./theme";

function useRecorder() {
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const chunksRef = useRef<Blob[]>([]);
  const [recording, setRecording] = useState(false);
  const [permissionError, setPermissionError] = useState<string | null>(null);
  const [audioURL, setAudioURL] = useState<string | null>(null);
  const [elapsed, setElapsed] = useState(0);
  const timerRef = useRef<number | null>(null);

  const start = async () => {
    setPermissionError(null);
    setAudioURL(null);
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      const mr = new MediaRecorder(stream);
      chunksRef.current = [];
      mr.ondataavailable = (e) => {
        if (e.data && e.data.size > 0) chunksRef.current.push(e.data);
      };
      mr.onstop = () => {
        const blob = new Blob(chunksRef.current, { type: "audio/webm" });
        setAudioURL(URL.createObjectURL(blob));
        chunksRef.current = [];
        stream.getTracks().forEach((t) => t.stop());
      };
      mediaRecorderRef.current = mr;
      mr.start();
      setRecording(true);
      setElapsed(0);
      timerRef.current = window.setInterval(
        () => setElapsed((s) => s + 1),
        1000
      ) as unknown as number;
    } catch (err: any) {
      setPermissionError(err?.message || "Microphone not available.");
    }
  };

  const stop = () => {
    mediaRecorderRef.current?.stop();
    setRecording(false);
    if (timerRef.current) window.clearInterval(timerRef.current);
  };

  const reset = () => {
    setAudioURL(null);
    setElapsed(0);
  };

  return { start, stop, reset, recording, permissionError, audioURL, elapsed };
}

const VoiceRecorder: React.FC<{
  onAttach?: (url: string | null) => void;
  label?: string;
}> = ({ onAttach, label = "Record a quick answer (optional)" }) => {
  const { start, stop, reset, recording, permissionError, audioURL, elapsed } =
    useRecorder();
  const [playing, setPlaying] = useState(false);
  const audioRef = useRef<HTMLAudioElement | null>(null);

  useEffect(() => {
    onAttach?.(audioURL || null);
  }, [audioURL]);

  useEffect(() => {
    const a = audioRef.current;
    if (!a) return;
    const onEnd = () => setPlaying(false);
    a.addEventListener("ended", onEnd);
    return () => a.removeEventListener("ended", onEnd);
  }, []);

  const mmss = (s: number) =>
    `${String(Math.floor(s / 60)).padStart(2, "0")}:${String(s % 60).padStart(
      2,
      "0"
    )}`;

  return (
    <div className="rounded-2xl border border-slate-300 p-3 md:p-4 bg-slate-50">
      <div className="flex items-center justify-between gap-2">
        <div>
          <p className="text-sm text-slate-700 font-medium">{label}</p>
          {permissionError && (
            <p className="text-xs text-amber-700 mt-1">
              {permissionError} – You can still type your response below.
            </p>
          )}
        </div>
        <div className="flex items-center gap-2">
          {!recording ? (
            <button
              onClick={start}
              className="inline-flex items-center gap-2 rounded-full px-3 py-2 text-sm font-semibold shadow-sm border border-lime-400"
              style={{ background: theme.accent, color: "white" }}
            >
              <Mic className="h-4 w-4" /> Record
            </button>
          ) : (
            <button
              onClick={stop}
              className="inline-flex items-center gap-2 rounded-full px-3 py-2 text-sm font-semibold bg-red-600 text-white shadow-sm"
            >
              <Square className="h-4 w-4" /> Stop
            </button>
          )}
        </div>
      </div>

      <div className="mt-3 flex items-center justify-between text-xs text-slate-600">
        <span>
          {recording
            ? "Recording…"
            : audioURL
            ? "Recorded clip"
            : "No recording yet"}
        </span>
        <span className="font-mono">{mmss(elapsed)}</span>
      </div>

      {audioURL && (
        <div className="mt-3 flex items-center gap-2">
          <audio ref={audioRef} src={audioURL} />
          {!playing ? (
            <button
              onClick={() => {
                setPlaying(true);
                audioRef.current?.play();
              }}
              className="inline-flex items-center gap-2 rounded-full px-3 py-1.5 text-xs font-semibold border border-slate-300 text-slate-800"
            >
              <Play className="h-4 w-4" /> Play
            </button>
          ) : (
            <button
              onClick={() => {
                audioRef.current?.pause();
                setPlaying(false);
              }}
              className="inline-flex items-center gap-2 rounded-full px-3 py-1.5 text-xs font-semibold border border-slate-300 text-slate-800"
            >
              <Pause className="h-4 w-4" /> Pause
            </button>
          )}
          <button
            onClick={() => {
              audioRef.current?.pause();
              setPlaying(false);
              reset();
              onAttach?.(null);
            }}
            className="inline-flex items-center gap-2 rounded-full px-3 py-1.5 text-xs font-semibold border border-slate-300 text-slate-800"
          >
            <Trash2 className="h-4 w-4" /> Remove
          </button>
        </div>
      )}
    </div>
  );
};

export default VoiceRecorder;
